<template>
  <div class="product">
    <div class="product-container mb-2">
      <div class="product-container__information text-center w-100 mt-auto">
        <p class="text-center h6 text-primary">На <strong>{{ product.gram }}</strong> гр. продукту</p>
        <div class="information-items d-flex">
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">БЖ</span>
            <span class="information__footer">{{ bxbjCalc(bxCalc(product.product_b), bjCalc(product.product_j))}}</span>
          </div>
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">У</span>
            <span class="information__footer">{{ byCalc(product.product_y)}}</span>
          </div>
          <div class="information flex-fill d-flex flex-column">
            <span class="information__header">ХЕ</span>
            <span class="information__footer">{{ allCalc(bxCalc(product.product_b),bjCalc(product.product_j),byCalc(product.product_y))}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="product-form">
        <div class="mb-3">
          <label for="productGram" class="form-label">Грамм</label>
          <input v-model="product.gram" type="number" class="form-control" id="productGram" placeholder="">
        </div>
        <div class="mb-3">
          <label for="productB" class="form-label">Белков на 100гр.</label>
          <input v-model="product.product_b" type="number" step="0.1" class="form-control" id="productB" placeholder="">
        </div>
        <div class="mb-3">
          <label for="productH" class="form-label">Жиров на 100гр.</label>
          <input v-model="product.product_j" type="number" step="0.1" class="form-control" id="productH" placeholder="">
        </div>
        <div class="mb-3">
          <label for="productY" class="form-label">Углеводов на 100гр.</label>
          <input v-model="product.product_y" type="number" step="0.1" class="form-control" id="productY" placeholder="">
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Calculator',
  data() {
    return {
      product: {
        gram: 100,
        product_b: 0,
        product_j: 0,
        product_y: 0,
      },
    }
  },
  computed: {
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    }
  },
  methods: {
    calculate(value) {
      return (parseFloat(value) * this.product.gram) / 100
    },
    bxCalc(value) {
      return ((this.calculate(value) * parseFloat(this.bx_param)) / 12).toFixed(1);
    },
    bjCalc(value) {
      return ((this.calculate(value) * parseFloat(this.bj_param)) / 12).toFixed(1);
    },
    byCalc(value) {
      return (this.calculate(value) / 12).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
  },
  mounted() {}
}
</script>

<style scoped lang="scss">
.product {
  .product-container {
    &__avatar {
      width: 100px;
      min-width: 100px;
    }

    &__information {
      height: 100px;

      .information {
        margin: 0 2px;

        .information__header {
          border-bottom: 1px solid green;
        }

        .information__footer {
          font-weight: 500;
          font-size: 18px;
        }
      }
    }
  }
}
</style>
